body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.window_header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.window_title_text {
  padding-right: 20px;
}

.window_title_buttons {
  margin-right: -6px;
  display: flex;
  justify-content: space-between;
}
.small_icon {
  padding-right: 5px;
  height: 20px;
}

.footer_buttons {
  width: 50%;
}

.footer_cut_out {
  display: flex;
  flex-grow: 1;
  flex-wrap: wrap;
  width: 600px;
}

@media only screen and (max-width: 400px)  {
  .footer_cut_out {
    width: 310px;
  }

  .footer_buttons {
    width: 100%;
  }
}

.popup_window_container {
  position: absolute;
  z-index: 4;
  padding: 10px;

  margin-left: -15px;
  margin-top: -25px;
}

.popup_movable_header {
  cursor: -webkit-grab;
  cursor: grab;
}

.popup_movable_header:active {
  cursor: -webkit-grabbing;
  cursor: grabbing;
}
.about_window {
  width: 570px;
}

.text_container {
  background: #fffee8;
  padding: 10px;

  margin-bottom: 15px;

  border: 1px solid;
  padding: 10px;
  box-shadow: 10px 10px #707070;
  font-size: 18px;
}

.avatar_picture {
  height: 210px;
  float: right;
  padding: 5px;
  margin-left: 2px;
}

@media only screen and (max-width: 400px)  {
  .about_window {
    width: 280px;
  }
}
.links_window {
  width: 570px;
}

.link_list {
  overflow: auto;
  overflow-y: hidden;
  color: white;
  background-color: #3968C2;
  font-weight: bold;

  font-family: Lucida Console,Lucida Sans Typewriter,monaco,Bitstream Vera Sans Mono,monospace;
}

.link_list a {
  color: yellow;
}

.link_list a:hover {
  color: red;
}

.link_list a:visited {
  color: lightgray;
}

.link_style {
  padding: 3px;
}

.globe_container {
  background-color: black;
  width: 80px;
  height: 80px;
  float: right;

  margin-left: 15px;
  margin-bottom: 15px;
}

.globe_picture {
  height: 70px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

@media only screen and (max-width: 400px)  {
  .links_window {
    width: 280px;
  }
}
.projects_window {
  width: 580px;
}

.cutout_area {
  overflow: auto;
  overflow-y: hidden;
  background-color: black;

  padding: 10px;

  color: white;
  font-size: 14px;
  font-weight: bold;
  font-family: Lucida Console,Lucida Sans Typewriter,monaco,Bitstream Vera Sans Mono,monospace;
}

.project_item {
  padding: 5px;
}

.bulletpoint {
  color: orange;
  font-weight: bold;
}

.comment_text {
  color: lime;
  font-style: italic;
}

.project_item a {
  color: yellow;
}

.project_item a:hover {
  color: red;
}

.project_item a:visited {
  color: lightskyblue;
}

.blink_text {
  -webkit-animation: blinker 500ms linear infinite;
          animation: blinker 500ms linear infinite;
}

@-webkit-keyframes blinker {
  50% {
    opacity: 0;
  }
}

@keyframes blinker {
  50% {
    opacity: 0;
  }
}


@media only screen and (max-width: 400px)  {
  .projects_window {
    width: 290px;
  }
}
.contact_window {
  width: 450px;
}

@media only screen and (max-width: 400px)  {
  .contact_window {
    width: 280px;
  }
}

.social_icon {
  width: 60px;
  margin: 5px;
  height: 60px;
  background-color: white;
}

.social_icons_container {
  padding-top: 10px;

  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.credits_window {
  width: 630px;
  padding-bottom: 5px;
}

.bulletpoint_container {
  background: #eeeeee;
  padding: 10px;

  margin-bottom: 10px;

  border: 1px solid;
  padding: 10px;
  box-shadow: 10px 10px #707070;
}

.paragraph_title {
  font-weight: bold;
}

.accordion_icon {
  height: 17px;
  margin-left: 5px;
}

.rotate {
  -webkit-transform: rotate(180deg);
  transform: rotate(180deg);
}

@media only screen and (max-width: 400px)  {
  .credits_window {
    width: 300px;
  }
}
.button_item {
  vertical-align: top;
  display: inline-block;
  text-align: center;
  width: 80px;
  height: 80px;
  margin: 5px;
}

.first_row_icons {
  position: absolute;
  top: 0;
  left: 0;
  margin: 5px
}

.last_row_icons {
  position: absolute;
  bottom: 0;
  right: 0;
  margin: 5px
}

.cut_out {
  width: 600px;
  height: 280px;
  background-color: white;
}

.movable_icon {
  cursor: -webkit-grab;
  cursor: grab;
}

.movable_icon:active {
  cursor: -webkit-grabbing;
  cursor: grabbing;
}

@media only screen and (max-width: 400px)  {
  .cut_out {
    width: 310px;
    height: 280px;
  }
}

.icon {
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none;   /* Chrome/Safari/Opera */    /* Konqueror */
  -moz-user-select: none;      /* Firefox */
  -ms-user-select: none;       /* Internet Explorer/Edge*/
   user-select: none;          /* Non-prefixed version, currently 
                                  not supported by any browser */
   -webkit-user-drag: none;
  -khtml-user-drag: none;
  -moz-user-drag: none;
  -o-user-drag: none;
  user-drag: none;

  height: 48px;
}

.icon_caption {
  font-weight: bold;
  display: block;
}
.notfound_window {
  width: 570px;
}

@media only screen and (max-width: 400px)  {
  .notfound_window {
    width: 280px;
  }
}
.copyright {
  font-size: 12px;
  position: absolute;
  text-shadow: -1px -1px 1px white;
  opacity: 0.6;
  filter: Alpha(opacity=60); /* IE8 and earlier */
}

@media only screen and (max-width: 400px)  {
  .copyright {
    font-size: 11px;
  }
}
.centered_item {
  position: absolute;
  top: 35%;
  left: 50%;
  color: red;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  text-align: center;

  -webkit-animation: blinker 0.5s linear infinite;

          animation: blinker 0.5s linear infinite;
}

@-webkit-keyframes blinker {
  50% {
    opacity: 0;
  }
}

@keyframes blinker {
  50% {
    opacity: 0;
  }
}
.window_centered {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 40px;
}
