.centered_item {
  position: absolute;
  top: 35%;
  left: 50%;
  color: red;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  text-align: center;

  -webkit-animation: blinker 0.5s linear infinite;

          animation: blinker 0.5s linear infinite;
}

@-webkit-keyframes blinker {
  50% {
    opacity: 0;
  }
}

@keyframes blinker {
  50% {
    opacity: 0;
  }
}